<template>
	<div id="events_statistics_container">
		<div class="type-title">
			<div>武将新皮肤数量统计</div>
			<van-icon @click="changeSheet" name="exchange" />
		</div>
		<div class="table-container">
			<table>
				<thead>
					<tr>
						<th>武将名称</th>
						<th colspan="2">新皮肤数量</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(item, index) in data.pfData" :key="index">
						<td>{{item.name}}</td>
						<td>{{item.count}}次</td>
						<td rowspan="3" class='date-list'>
							<div v-for="(v, i) in item.list" :key="i">{{v.name}}·{{item.name}}</div>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script setup>
	import {reactive} from 'vue';
	import {useRouter} from 'vue-router';
	import pfData from '@/mock/archive/pf-data';
	
	const router = useRouter();
	const data = reactive({
		pfData: []
	})
	
	let arr = [];
	Object.keys(pfData).forEach( key => {
		console.log(key);
		arr.push({
			name: key,
			count: pfData[key].count,
			list: pfData[key].list
		})
	})
	arr.sort( (a, b) => {
		return b.count - a.count;
	})
	data.pfData = arr;
	
	const changeSheet = () => {
		router.push({path: '/events-statistics/wj'});
	}
	
</script>

<style lang="less" scoped>
	#events_statistics_container {
		height: 100%;
		display: flex;
		flex-direction: column;

		.type-title {
			padding: 6px 24px;
			text-align: center;
			border-radius: 6px;
			background-color: rgba(0, 140, 140, 0.75);
			color: #fff;
			font-size: 16px;
			flex-shrink: 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
		}

		.table-container {
			flex-grow: 1;
			padding: 12px;
			border-radius: 8px 10px;
			background-color: rgba(0, 140, 140, 0.75);
			color: #fff;
			margin-top: 6px;
			overflow: hidden;

			table {
				width: 100%;
				height: 100%;
				display: flex;
				flex-direction: column;

				tr {
					display: flex;
					align-items: center;

					td {
						padding: 4px 8px;
					}

					th:nth-of-type(1),
					td:nth-of-type(1) {
						width: 100px;
						flex-shrink: 0;
						text-align: left;
					}

					th:nth-of-type(2) {
						flex-grow: 1;
						text-align: left;
					}

					td:nth-of-type(2) {
						width: 50px;
						flex-shrink: 0;
					}

					td:nth-of-type(3) {
						flex-grow: 1;
						text-align: left;
					}
				}

				thead {
					tr {
						padding: 0 0 8px 0;
					}
				}

				tbody {
					flex-grow: 1;
					overflow-y: auto;

					tr {
						border-top: 2px solid #fff;
						padding-bottom: 4px;

						td.date-list {
							div:not(:first-child) {
								margin-top: 4px;
							}
						}
					}
				}
			}
		}
	}
</style>